<template>
  <div class="home-container">
    <div class="container" :class="[folding?'container-collapse':'']">
      <v-head></v-head>
      <div class="content">
          <div class="content-main">
            <transition name="el-fade-in-linear" mode="out-in">
                <router-view></router-view>
            </transition>
          </div>
      </div>
      <div class="footer">Copyright 2007-2020 顺的网络. All Rights Reserved</div>
    </div>
    <v-sidebar></v-sidebar>
  </div>
</template>

<script>
import bus from "../common/js/bus";
import vHead from "../common/Head";
import vSidebar from "../common/Sliderbar";
export default {
  components: {
    vHead,
    vSidebar
  },
  data() {
    return {
      tagsList: [],
      folding: true
    };
  },
  created: function() {
    bus.$on("content-main", result => {
      console.log("result", result);
      this.folding = result;
    });
    // console.log("$$$$$",this.$refs.box)
    bus.$emit("content",this.$refs.content);
  }
};
</script>


